import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import styles from './Navbarstyle.module.css';
import 'bootstrap/dist/css/bootstrap.min.css'; 

// Bootstrap for react 
import Container from 'react-bootstrap/Container'; 
import Button from 'react-bootstrap/Button';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Nav from 'react-bootstrap/Nav';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';

class NavBar extends Component{
    constructor(props){
        super(props);
    }

    render(){
        return(
            <div>
                <Navbar bg="dark" variant="dark" expand="md" className={styles.navbar}>
                    <Navbar.Brand href="#">H4RSH PATEL</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mx-auto">
                        <Nav.Link href="#" className="mx-2 text-center">HomePage</Nav.Link>
                        <Nav.Link href="#" className="mx-2 text-center">Popular</Nav.Link>
                        <Nav.Link href="#" className="mx-2 text-center">Recent</Nav.Link>
                        <Nav.Link href="#" className="mx-2 text-center">Blog</Nav.Link>
                        <Nav.Link href="#" className="mx-2 text-center">About</Nav.Link>
                        <NavDropdown title="Others" id="basic-nav-dropdown" className="mx-2 text-center">
                            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        </NavDropdown>
                        </Nav>
                        <Form inline>
                            <FormControl style={{margin:"8px 12px"}} type="text" placeholder="johndoe@example.com" />
                            <Button style={{margin:"8px 12px"}} variant="outline-danger">Subscribe</Button>{' '}
                        </Form>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        );
    }
}

export default NavBar