import React, {Component} from 'react'; 
import ReactDOM from 'react-dom';
import styles from './Footerstyle.module.css';
import 'bootstrap/dist/css/bootstrap.min.css'; 

import Container from 'react-bootstrap/Container'; 
import Row from 'react-bootstrap/Row'; 
import Col from 'react-bootstrap/Col'; 
import Button from 'react-bootstrap/Button'; 
import InputGroup from 'react-bootstrap/InputGroup'; 
import FormControl from 'react-bootstrap/FormControl'; 
import ListGroup from 'react-bootstrap/ListGroup';

class Footer extends Component{
    render(){
        return(
            <div style={{width:'100%',backgroundColor:'rgb(51,51,51)'}}>
            <Container className={styles.container}>
                <h4 className={styles.heading}>Contact Me</h4>
                <Row class={styles.row}>
                    <Col>
                    <ListGroup className={styles.listGroup} variant="flush">
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#" class="nav-link">
                                    <i class="fa fa-facebook text-white" aria-hidden="true">Facebook</i>
                                </a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#" class="nav-link">
                                    <i class="fa fa-instagram text-white" aria-hidden="true">Instagram</i>
                                </a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#" class="nav-link">
                                    <i class="fa fa-linkedin text-white" aria-hidden="true">LinkedIn</i>
                                </a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#" class="nav-link">
                                    <i class="fa fa-github text-white" aria-hidden="true">Github</i>
                                </a>
                        </ListGroup.Item>
                    </ListGroup>
                    </Col>
                </Row>
                <div className={styles.line}></div>
            </Container>
            <Container className={styles.container}>
                <h4 className={styles.heading}>Other Links</h4>
                <Row className={styles.row}>
                    <Col>
                    <ListGroup className={styles.listGroup} variant="flush">
                        <ListGroup.Item className={styles.listItem}>
                            <h5>Heading 1</h5>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 1</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 2</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 3</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 4</a>
                        </ListGroup.Item>
                    </ListGroup>
                    </Col>
                    <Col>
                    <ListGroup className={styles.listGroup} variant="flush">
                        <ListGroup.Item className={styles.listItem}>
                            <h5>Heading 2</h5>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 1</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 2</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 3</a>
                        </ListGroup.Item>
                    </ListGroup>
                    </Col>
                    <Col>
                    <ListGroup className={styles.listGroup} variant="flush">
                        <ListGroup.Item className={styles.listItem}>
                            <h5>Heading 3</h5>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 1</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 2</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 3</a>
                        </ListGroup.Item>
                        <ListGroup.Item className={styles.listItem}>
                            <a href="#">Link 4</a>
                        </ListGroup.Item>
                    </ListGroup>
                    </Col>
                </Row>
                <div className={styles.copyright}>Copyright © Harsh Patel 2021</div>
            </Container>
            </div>
        );
    }
}

export default Footer;